button {
  min-width: 100px;
  min-height: 30px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  color: #fff;
  cursor: pointer;
  background: #4676d7;
  border: 0;
  border-radius: 5px;
  margin-left: 10px;
  font-family: inherit;
}

button:hover {
  background: #1d49aa;
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 4px #cbd6ee;
}

input {
  min-width: 100px;
  min-height: 25px;
  border-radius: 5px;
  box-shadow: 0 0 0 2px #cbd6ee;
}

label {
  font-size: 15px;
  font-weight: 600;
  line-height: 13px;
}

